.comment-form-initial {
	position: relative;
	display: flex;
	height: 140px;
	width: 100%;
	overflow-y: hidden;
	flex-direction: row;
	justify-content: space-around;
	visibility: hidden;
}
.make-new-comment {
	position: absolute;
	visibility: hidden;
	left: 0;
	display: flex;
	flex-direction: row;
	align-items: center;
}

.add-visible {
	visibility: visible;
}

.form-visible {
	visibility: visible;
}

.add-comment {
	width: 50px;
	height: 50px;
	margin: 0 1rem;
	cursor: pointer;
}

.first-column {
	width: 20%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}

.name-input-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.name-input {
	width: 100%;
	outline-color: rgb(188, 188, 255);
	border: 0.2px solid rgb(187, 187, 187);
}
.message-input {
	display: flex;
	flex-direction: column;
	width: 75%;
	align-items: center;
	margin-right: 0;
	outline-color: rgb(188, 188, 255);
	border: 0.2px solid rgb(187, 187, 187);
}

.submit-button {
	background-color: rgb(174, 223, 223);
	border-radius: 10%;
	border: none;
	outline: none;
	cursor: pointer;
	width: 5rem;
}

form label {
	padding: 0, 0, 0.5rem 0;
}

.comments-title {
	position: relative;
	font-size: 2rem;
	text-align: center;
	padding: 0 0 0.5rem 0;
	font-family: "Raleway", sans-serif;
}

.comment-form-initial {
	padding: 0.5rem;
}

@media screen and (max-width: 1100px) {
	.message-input {
		width: 60%;
		font-size: 1rem;
	}
	.first-column {
		width: 40%;
	}
	.submit-button {
		height: 2rem;
		font-size: 1rem;
	}
	.name-input {
		width: 80%;
		height: 2rem;
		font-size: 1rem;
	}
	.comment-form-initial {
		padding: 0.5rem 0;
	}
}
